<template lang="pug">
.comp-form-select
    van-cell(:class="{noval:myValue===''}" :title="label" :value="displayValue" is-link @click="show=true")
    van-actionsheet(v-model="show" :actions="actions" @select="onSelect")
</template>

<script>
export default {
    props: {
        label: {
            type: String,
            default: ''
        },
        actions: {
            type: Array,
            default () {
                return [
                    {name: '1'}
                ];
            }
        },
        placeholder: {
            type: String,
            default: ''
        },
        value: {
            type: String,
            default: ''
        }
    },
    watch: {
        value (v) {
            let find = this.actions.find(item => {
                return item.value === v;
            });
            this.myValue = find.value;
            this.valueText = find.name;
        }
    },
    computed: {
        displayValue () {
            return this.valueText || this.placeholder;
        }
    },
    data () {
        return {
            myValue: '',
            valueText: '',
            show: false
        };
    },
    methods: {
        onSelect (item) {
            this.myValue = item.value;
            this.valueText = item.name;
            this.show = false;
            this.$emit('input', this.myValue);
        }
    }
};
</script>
<style lang="scss">
.comp-form-select {
    .noval {
        .van-cell__value {
            color: rgb(117, 117, 117);
        }
    }
    .van-cell__title {
        max-width: 90px;
    }
    .van-cell__value {
        text-align: left;
    }
}
</style>



