<template lang="pug">
.comp-my-nav-bar
    van-nav-bar(:fixed="true" :title="title" left-text="返回" left-arrow @click-left="handleLeftClick")
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: ''
        }
    },
    methods: {
        handleLeftClick () {
            history.back();
        }
    }
};
</script>

<style lang="scss" scoped>
.comp-my-nav-bar {
    height: 46px;
}
</style>



